import React from 'react';
import{Contact}from'./Contact';

export const GeneralContact=(props)=>{
  return (
   <div className='general'>
    <p className="general_title">General</p>
   {props.contacts.map((pr,index)=><Contact contact={pr} key={index} 
   handleToggleFavorite={props.handleToggleFavorite} 
   handleDeleteContact={props.handleDeleteContact}
   handleUpdateContact={props.handleUpdateContact}
   />)}
   </div>
  );
}
