import React from "react";
import { useState } from "react";
import { AddRandomContact } from "./AddRandomContact";
import { RemoveAllContact } from "./RemoveAllContact";
import { AddContact } from "./AddContact";
import { FavoriteContact } from "./FavoriteContact";
import { GeneralContact } from "./GeneralContact";
import { getRandomUser } from "../../utility/api";

export const ContactIndex = () => {
  const contactList = [
    {
      id: 1,
      name: "Ben Parker",
      phone: "555-555-7770",
      email: "ben@dotnetmastery.com",
      isFavorite: false,
    },

  ];
  const [state, setState] = useState(contactList);
  const [update,setUpdate]=useState(false);
  const [objUp,setObjUp]=useState({});
  const handleToggleFavorite = (contact) => {
    const newState = state.map((con) => {
      if (con.id == contact) {
        return { ...con, isFavorite: !con.isFavorite };
      } else {
        return con;
      }
    });
    setState(newState);
  };
  const handleDeleteContact = (contact) => {
    const newState = state.filter((con) => !(con.id == contact));
    setState(newState);
  };
  const GetRandomContact = async () => {
    const response = await getRandomUser();
    handleAddContact({
      email: response.data.email,

      name: response.data.first_name,
      phone: response.data.phone_number,
    });
  };

  const handleAddRandomContact = () => {
    GetRandomContact();
  };
  const handleRemoveAll=()=>{
    setState(contactList)
  }
 
  const handleUpdateContact=(contact)=>{
    setUpdate(true);
    console.log(contact.name);
    setObjUp(contact);
    
  }

  const handleAddContact = (newContact) => {
    if (newContact.name == "") {
      return { status: "failure", msg: "Please Enter a valid Name" };
    } else if (newContact.phone == "") {
      return { status: "failure", msg: "Please Enter a valid Phone Number" };
    }
    const duplicateRecord = state.filter((x) => {
      if (x.name == newContact || x.phone == newContact.phone) {
        return true;
      }
    });
    if (duplicateRecord.length > 0) {
      return { status: "failure", msg: "Duplicate Record" };
    } else {
      const newFinalContact = {
        ...newContact,
        id: state[state.length - 1].id + 1,
        isFavorite: false,
      };
      setState(state.concat([newFinalContact]));
      return { status: "success", msg: "Contact was added successfully" };
    }
  };
  return (
    <div className="contactIndex container">
      <div className="contactIndex_row ">
        <AddRandomContact handleAddRandomContact={handleAddRandomContact} />
        <RemoveAllContact handleRemoveAll={handleRemoveAll}/>
      </div>
      <AddContact handleAddContact={handleAddContact} update={update} setUpdate={setUpdate} objUp={objUp} />
      <FavoriteContact
        contacts={state.filter((con) => con.isFavorite === true)}
        handleAddContact={handleAddContact}
        handleToggleFavorite={handleToggleFavorite}
        handleDeleteContact={handleDeleteContact}
        handleUpdateContact={handleUpdateContact}
      />
      <GeneralContact
        contacts={state.filter((con) => con.isFavorite === false)}
        handleAddContact={handleAddContact}
        handleToggleFavorite={handleToggleFavorite}
        handleDeleteContact={handleDeleteContact}
        handleUpdateContact={handleUpdateContact}
      />
    </div>
  );
};
