import React,{useState} from 'react';

export const AddContact=(props)=>{
   const[mes,setMes]=useState('....')
  
  const handleAddContactFormSubmit=(e)=>{
    e.preventDefault();
    const name=e.target.elements.contactName.value.trim();
    const email=e.target.elements.contactEmail.value.trim();
    const phone=e.target.elements.contactPhone.value.trim();
    const response=props.handleAddContact({name:name,email:email,phone:phone});
    if(response.status)
    {
      setMes(response.msg)
      if(response.status=='success'){
        document.querySelector('.contact-form').reset();
      }
     
    }
    
  }
 
  return (
   <div>
    <p>{mes}</p>
    <form className='contact-form' onSubmit={handleAddContactFormSubmit}>
    <div className="addContact">
      <p className="addContact_title">{props.update?'Update Contact':'Add a new Contact'}</p>
      <div className="addContact_row">
        <input type="text" 
        className="form-control"
         placeholder='Name...' 
         name='contactName'
         defaultValue={props.update?props.objUp.name:''}
         />
        <input type="text" 
        className="form-control" 
        placeholder='Email...' 
        name='contactEmail'
        defaultValue={props.update?props.objUp.email:''}
        />
        <input type="text"
         className="form-control" 
         placeholder='Phone...' 
         name='contactPhone'
         defaultValue={props.update?props.objUp.phone:''}
         />
      </div>
      {props.update?<span style={{display:'flex',gap:'10px'}}><button className="btn btn-success form-control" >Update</button>
      <button className="btn btn-danger form-control"onClick={()=>props.setUpdate(false)} >Cancel</button></span>:<button className=" btn btn-primary form-control" >Create</button>}
      
    </div>
    </form>

   </div>
  );
}
