import React from "react";

export const Contact = (props) => {
 
  return (
    <div className="contact">
      <img
        src={`https://ui-avatars.com/api/?name=${props.contact.name}`}
        alt=""
      />
      <div className="contact_descr">
        <span className='text-warning'>{props.contact.name}</span>
        <span>{props.contact.email}</span>
        <span>{props.contact.phone}</span>
      </div>

      <button
        className={`btn btn-sm m-1 ${
          props.contact.isFavorite ? "btn-warning" : "btn-outline-warning"
        }`}
        onClick={()=>props.handleToggleFavorite(props.contact.id)}
      >
        <i className="bi bi-star"></i>
      </button>
      <div className="contact_select">
        <button className="btn btn-primary btn-sm m-1" onClick={()=>props.handleUpdateContact(props.contact)}>
          <i className="bi bi-pencil-square" ></i>
        </button>
        <button className="btn btn-danger btn-sm m-1"  onClick={()=>props.handleDeleteContact(props.contact.id)}>
          <i className="bi bi-trash-fill"></i>
        </button>
      </div>
    </div>
  );
};
